// Search


.searched {
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.searchedBox {
  animation: searchedBox-reveal 1s;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  color: white;
  height: 32px;
  min-width: 32px;
  border-radius: 8px;
  padding: 8px;
//   box-shadow: 0 1.5px 3px rgba(0, 0, 0, 0.24), 0 1.5px 3px rgba(0, 0, 0, 0.32);
  transition: all 0.8s;
  &:hover {
    //box-shadow: 0 3px 4.5px rgba(0, 0, 0, 0.32), 0 3px 4.5px rgba(0, 0, 0, 0.4);
  }
  &:hover > .searchedInput {
    transition: all 0.8s cubic-bezier(0.16, 0.2, 0.32, 1.4);
    width: 120px;
    padding: 2px 2px;
    
  }
  @media only screen and (max-width: 885px) {
      &:hover > .searchedInput {
            width: 50px;
      }
  
  }
}

.searchedInput {
  background: none;
  border: none;
  outline: none;
  float: left;
  color: white;
  font-size: 16px;
  transition: 0.4s;
  width: 0px;
  font-family: "Lexend Deca";
  padding: 8px 0px;
  
  &:valid > .searchedBox {
    box-shadow: 0 3px 4.5px rgba(0, 0, 0, 0.32), 0 3px 4.5px rgba(0, 0, 0, 0.4);
  }
}

.searchedButton {
  color: white;
  width: 32px;
  height: 32px;
  background-color: transparent;
  outline: none;
  border: none;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  //margin-left: 0.5px;
  cursor: pointer;
}

input::placeholder {
  color: white;
  font-family: "Lexend Deca";
  opacity: 0.5;
}


//// next-prev

.post-nav {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}

.post-nav p {
    flex: 1 1 0;
    width: 45%;
}

.post-nav p:first-child {
    padding-right: 0.5em;
}

.post-nav p:last-child {
    padding-left: 0.5em;
    text-align: right;
}


.post-nav p a {
    color: #d7dcdf;
    cursor: pointer;
    text-decoration: none;
    background-color: #434a4e;
    padding: 0.3em;
    border-radius: 0.1em;
}

.post-nav p a:hover {

    background: #344154;
    box-shadow: 0 0 9px 0px #02046845;
    
}

.embed-pdf{
    position: relative;
    display: block;
    height: 550px !important;
    padding: 1%;
    overflow: hidden;
}